.alert {
    font-family: sans;;
    font-size: 11px;
    font-weight: 600;
    text-transform: uppercase;
    margin-bottom: 10px;
    padding: 10px;
    border: solid 2px;
    border-radius: 3px;
}

.alert-danger {
    background-color: rgba(255, 0, 0, 0.2);
    color: rgb(255, 0, 0);
    border-color: rgb(255, 0, 0);
}


.alert-success {
    background-color: rgba(0, 255, 0, 0.2);
    color: #29b003;
    border-color: #47d91e;
}

